#event-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
#eventdiv-heading{
    font-family: Heavitas;
    font-size: 5vh;
    text-wrap: wrap;
    text-decoration: underline;
}
#event1-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    
    width: 80%;
    background-color: var(--col-bg-blue);
    opacity: 85%;
    border-radius: 25px;
    transition: 0.3s ease;
}
#event1-container:hover {
    transform: scale(1.02);
}
#event1-subcontainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}
#event1-title{
    font-family: Heavitas;
    font-size: 4vh;
    text-wrap: wrap;
    color: var(--col-fg-accent);
}
#event-1-image-div{
    width: 80%;
}
#showcase-logo{
    width: 100%;
    border-radius: 30px;
}
#event-1-desc-div{
    width: 82%;
    text-align: justify;
    font-family: Montserrat-SemiBold;
}

#event-1-desc-div > p > ul > li {
    text-align: left;
}

#event2-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    background-color: var(--col-bg-orange);
    opacity: 85%;
    border-radius: 25px;
    margin: 4vh 0 0 0;
    transition: 0.3s ease;
}
#event2-container:hover {
    transform: scale(1.02);
}
#event2-subcontainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}
#event2-title{
    font-family: Heavitas;
    font-size: 4vh;
    text-wrap: wrap;
    color: var(--col-fg-orange);
}
#event-2-image-div{
    width: 80%;
}
#roadster-logo{
    width: 100%;
    border-radius: 30px;
}
#event-2-desc-div{
    width: 82%;
    text-align: justify;
    font-family: Montserrat-SemiBold;
    font-size: 2.25vh;
}

/* DESKTOP CSS  */
@media (min-aspect-ratio: 1.4){
    #event-container{
        margin: 0;
    }
    #eventdiv-heading{
        font-size: 8vh;
    }
    /* #event1-container{
        padding-right: 4vh;
    } */
    #event1-subcontainer{
        flex-direction: row;
        align-items: center;
        margin: 2vh 4vh;
    }
    #event1-title{
        font-size: 5vh;
        margin: 2vh 0 0 0;
    }
    #event-1-image-div{
        width: 100%;
    }
    #showcase-logo{
        transform: scale(0.7);
    }
    #event-1-desc-div{
        width: 100%;
        text-align: justify;
        font-size: 2.25vh;
        padding: 1vh 0 0;
    }
    #event2-subcontainer{
        flex-direction: row;
        align-items: center;
        margin: 2vh 4vh;
    }
    #event2-title{
        font-size: 5vh;
        margin: 2vh 0 0 0;
    }
    #event-2-image-div{
        width: 100%;
    }
    #roadster-logo{
        transform: scale(0.7);
    }
    #event-2-desc-div{
        width: 100%;
        text-align: justify;
        font-size: 2.25vh;
        padding: 1vh 1vh;
    }
}