#section1{
  width: 100%;
  text-align: center;
  /* background-color: black; */
  /* background-image: url('./../../Resources/Tinted-Bkg.jpeg'); */
  /* height: 68vh; */
  margin: 0;
}
.section1-subheading{
  font-family: Heavitas;
  color: var(--col-bg-blue);
  font-size: 5vh;
  margin: 0 0 0 0;
  text-shadow: -4px -4px 0 #101a2d, 4px -4px 0 #101a2d, -4px 4px 0 #101a2d, 4px 4px 0 #101a2d;

}
.section1-heading{
  font-family: TheBoldFont;
  font-size: 7vh;
  margin: 1vh 0 1vh 0;
  text-wrap: wrap;
}
#Main-Heading-Landing2 {
  font-size: 5vh;
  margin: 0.5vh 0 0.5vh 0;
}
.primary-btn{
  background-color: var(--col-bg-blue);
  padding: 2vh 4vh;
  margin-top: 3vh;

  font-family: CocogoosePro;
  color: #fff;
  font-size: 2.5vh;

  border: none;
  cursor: pointer;
  border-radius: 50px;
  transition: 0.3s ease;
}
.primary-btn:hover {
    background-color:#1a4491;
  }

#s2-text-div{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  font-family: TheBoldFont;
  margin: 2vh 0 0 0;
}
#s2-headingl2-s1{
    font-size: 8vh;
    background: url(./../../Resources/ComicBkg.jpeg);
    background-size: cover;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    margin: 0 1vh 0 0;
}
#s2-headingl2-s2{
    margin: 0 0 0;
    font-size: 8vh;
    background: url(./../../Resources/ComicBkg.jpeg);
    background-size: cover;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
}
/* CSS FOR DESKTOP NOW */
@media (min-aspect-ratio: 0.5){
  #section1{
    padding: 2.5vh 0 0 0;
  }
  .section1-subheading{
    font-size: 7vh;
    margin: 3vh 0 2vh 0;
  }
  .section1-heading{
    font-size: 7.5vh;
    margin: 4vh 0 0.5vh 0;
  }
  #Main-Heading-Landing2{
    font-size: 6vh;
    margin: 1vh 0 1vh 0;
  }
  .primary-btn{
    padding: 3vh 6vh;
    margin-top: 4vh;
    font-size: 3.2vh;
  }
  #s2-headingl2-s1{
    font-size: 11vh;
  }
  #s2-headingl2-s2{
    font-size: 11vh;
  }
}
@media (min-aspect-ratio: 1){
  #section1{
    padding: 3vh 0 0 0;
  }
  .section1-subheading{
    font-size: 7.5vh;
    margin: 3vh 0 2vh 0;
  }
  .section1-heading{
    font-size: 8vh;
    margin: 4vh 0 0.5vh 0;
  }
  #Main-Heading-Landing2{
    font-size: 6vh;
    margin: 1vh 0 1vh 0;
  }
  .primary-btn{
    padding: 3.5vh 6.5vh;
    margin-top: 4vh;
    font-size: 3.2vh;
  }
  #s2-headingl2-s1{
    font-size: 12vh;
  }
  #s2-headingl2-s2{
    font-size: 12vh;
  }
}
@media (min-aspect-ratio: 1.4){
  #section1{
    padding: 10vh 0 0 0;
  }
  .section1-subheading{
    font-size: 9vh;
    margin: 4vh 0 2vh 0;
  }
  .section1-heading{
    font-size: 10vh;
    margin: 4vh 0 0.5vh 0;
  }
  #Main-Heading-Landing2{
    font-size: 7vh;
    margin: 1vh 0 1vh 0;
  }
  .primary-btn{
    padding: 4vh 7vh;
    margin-top: 4vh;
    font-size: 3.4vh;
  }
  #s2-headingl2-s1{
    font-size: 14vh;
  }
  #s2-headingl2-s2{
    font-size: 14vh;
  }
}